import React, { useState } from 'react';
import { WelcomeLayout } from 'shared/layouts/WelcomeLayout';
import { useFormik } from 'formik';
import * as yup from 'yup';
import { useDispatch } from 'react-redux';
import { KntInput, KntPhoneInput } from 'shared/components/Fields';
import Actions from 'store/actions';
import { useAppState } from 'shared/state';
import { RegisterInfo } from 'shared/state/reducer';
import { AxiosError } from 'axios';
import { Link } from 'react-router-dom';
import { Helmet } from 'react-helmet';
const FormRequest = Actions.entity.formRequest;

const phoneRegExp =
	/^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/;

const validationSchema = yup.object({
	username: yup.string().email('Enter a valid email').required('Email is required'),
	first_name: yup.string().trim().required('Firstname is required'),
	last_name: yup.string().trim().required('Lastname is required'),
	mobile: yup
		.string()
		.required('Mobile number is required')
		.matches(phoneRegExp, 'Mobile number is not in correct format')
		.min(10, 'Too short'),
	password: yup
		.string()
		.strict(true)
		.trim('Password cannot be blank')
		.min(6, 'Password should be of minimum 6 characters length')
		.required('Password is required'),
	passwordConfirmation: yup
		.string()
		.oneOf([yup.ref('password')], 'Passwords are not the same!')
		.required('Password confirmation is required!')
});

function Signup() {
	const dispatch = useDispatch();
	const { setRegisterInfo } = useAppState();
	const [existError, setExistError] = useState(false);
	const [isRegistered, setIsRegistered] = useState(false);
	const formik = useFormik({
		initialValues: {
			username: '',
			password: '',
			passwordConfirmation: '',
			first_name: '',
			last_name: '',
			mobile: ''
		},
		validationSchema: validationSchema,
		onSubmit: (values, { setFieldError, setSubmitting }) => {
			const filteredValues = {
				...values
			};

			setExistError(false);
			// @ts-ignore
			delete filteredValues.passwordConfirmation;

			dispatch(
				FormRequest({
					entity: 'signup',
					name: 'signup',
					url: '/user/register-individual/',
					method: 'post',
					params: {},
					values: filteredValues,
					cb: {
						success: (data: any) => {
							setRegisterInfo(data as RegisterInfo);
							setSubmitting(false);
							setIsRegistered(true);
						},
						error: (error: AxiosError) => {
							if (error.response?.data?.code === 'username_exist_not_register') {
								dispatch(
									FormRequest({
										entity: 're-signup',
										name: 're-signup',
										url: '/user/re-register-individual/',
										method: 'patch',
										params: {},
										values: filteredValues,
										cb: {
											success: (data: any) => {
												if (process.env.NODE_ENV === 'development') {
													console.log('re-registered data', data);
												}

												setRegisterInfo(filteredValues);
												setIsRegistered(true);
											},
											error: (error: AxiosError) => {
												console.dir('error obj', error);
												if (error.response?.data?.code === 'username_exist') {
													setExistError(true);
												}
											},
											finally: () => {
												setSubmitting(false);
											}
										}
									})
								);
							}
							if (error.response?.data?.code === 'username_exist') {
								setExistError(true);
							}

							setSubmitting(false);
						},
						finally: () => {}
					}
				})
			);
		}
	});

	if (process.env.NODE_ENV === 'development') {
		console.log('formik values', formik.values);
	}

	return (
		<WelcomeLayout
			title={'Get started with KENNEKT'}
			description={'The most effective business management system'}
			goToPageLink={'/signin'}
			goToPage={'Already have an account?'}
			goToPageLinkText={'Sign In'}
			back
			content={
				<>
					<Helmet>
						<title>KENNEKT | Sign up</title>
					</Helmet>

					<form onSubmit={formik.handleSubmit}>
						<div className=" w-full font-poppins md:w-[423px]">
							{isRegistered ? (
								<>
									<p className="font-medium">
										A confirmation email has been sent to your email. Please, follow instructions to
										activate your account.
									</p>
								</>
							) : (
								<>
									<KntInput
										name={'username'}
										placeholder={'Enter your email'}
										label={'Email address'}
										required
										onChange={formik.handleChange}
										type={'email'}
										value={formik.values.username}
										error={formik.touched.username && Boolean(formik.errors.username)}
										helperText={formik.touched.username && formik.errors.username}
									/>
									<KntInput
										name={'first_name'}
										placeholder={'Enter your first name'}
										label={'First name'}
										required
										onChange={formik.handleChange}
										value={formik.values.first_name}
										error={formik.touched.first_name && Boolean(formik.errors.first_name)}
										helperText={formik.touched.first_name && formik.errors.first_name}
									/>
									<KntInput
										name={'last_name'}
										placeholder={'Enter your last name'}
										label={'Last name'}
										required
										onChange={formik.handleChange}
										value={formik.values.last_name}
										error={formik.touched.last_name && Boolean(formik.errors.last_name)}
										helperText={formik.touched.last_name && formik.errors.last_name}
									/>
									<KntPhoneInput
										name={'mobile'}
										placeholder={'Enter your mobile phone number'}
										label={'Phone number'}
										required
										setPhoneValue={formik.setFieldValue}
										value={formik.values.mobile}
										error={formik.touched.mobile && Boolean(formik.errors.mobile)}
										helperText={formik.touched.mobile && formik.errors.mobile}
										enableSearch
									/>
									<KntInput
										name={'password'}
										placeholder={'Password'}
										label={'Password'}
										required
										onChange={formik.handleChange}
										type={'password'}
										value={formik.values.password}
										error={formik.touched.password && Boolean(formik.errors.password)}
										helperText={formik.touched.password && formik.errors.password}
									/>
									<KntInput
										name={'passwordConfirmation'}
										placeholder={'Confirm password'}
										label={'Password confirmation'}
										required
										onChange={formik.handleChange}
										type={'password'}
										value={formik.values.passwordConfirmation}
										error={
											formik.touched.passwordConfirmation &&
											Boolean(formik.errors.passwordConfirmation)
										}
										helperText={
											formik.touched.passwordConfirmation && formik.errors.passwordConfirmation
										}
										classNames={'mb-8'}
									/>
									{existError ? (
										<p className={'mb-5 text-sm text-red-500'}>
											User account already exists.{' '}
											<Link to={'/request-password'} className={'underline hover:no-underline'}>
												Would like recover password?
											</Link>
										</p>
									) : null}
									<button
										disabled={formik.isSubmitting}
										className={
											'bg-primary py-3.5 uppercase transition ' +
											'w-full rounded-lg text-center text-sm font-light text-white hover:bg-primary-dark'
										}
									>
										{formik.isSubmitting ? 'Signing up. Please wait a while' : 'Sign up'}
									</button>
									<p className={'mt-5 text-center text-xs font-light text-kgrey-welcome '}>
										By signing in you agree to KENNEKT's
									</p>
								</>
							)}
						</div>
					</form>
				</>
			}
		/>
	);
}

export default Signup;
